        // 既往病史
        previousDiagnosis: '',
        // 既往诊断
        previousTreatment: '',
        // 曾接受的治疗
        treatmentEffect: '' // 治疗效果
      },

      // 脏腑相关症状选项
      zangfuSymptoms: [{
        label: '心悸胸闷（心）',
        value: '心悸胸闷（心）'
      }, {
