      }, {
        label: '耳鸣耳聋（肾）',
        value: '耳鸣耳聋（肾）'
      }, {
        label: '便秘腹泻（肠）',
        value: '便秘腹泻（肠）'
      }],
      // 全身伴随症状选项
      generalSymptoms: [{
        label: '发热（低热/高热）',
        value: '发热（低热/高热）'
      }, {
