        value: '面色苍白/萎黄'
      }, {
        label: '心烦易怒',
        value: '心烦易怒'
      }],
      // 饮食睡眠二便选项
      dailySymptoms: [{
        label: '食欲不振',
        value: '食欲不振'
      }, {
        label: '口苦口黏',
        value: '口苦口黏'
      }, {
        label: '失眠多梦',
        value: '失眠多梦'
      }, {
        label: '嗜睡易醒',
        value: '嗜睡易醒'
      }, {
        label: '大便干结',
        value: '大便干结'
      }, {
        label: '大便溏稀',
        value: '大便溏稀'
      }, {
        label: '尿频尿急',
        value: '尿频尿急'
      }, {
        label: '夜尿增多',
        value: '夜尿增多'
      }],
      // 中医体质类型选项
      constitutionTypes: [{
        label: '平和质',
