        value: '气郁质'
      }],
      // 发病诱因选项
      onsetFactors: [{
        label: '外感风寒',
        value: '外感风寒'
      }, {
        label: '外感风热',
        value: '外感风热'
      }, {
        label: '饮食不节（过饥/过饱）',
        value: '饮食不节（过饥/过饱）'
      }, {
        label: '情志不畅（生气/焦虑）',
        value: '情志不畅（生气/焦虑）'
      }, {
        label: '劳累过度',
        value: '劳累过度'
      }, {
        label: '熬夜失眠',
        value: '熬夜失眠'
      }, {
        label: '气候变化',
        value: '气候变化'
      }, {
        label: '不明原因',
        value: '不明原因'
      }]
    };
  },
  methods: {
    // 验证输入信息是否完整
    validateInput() {
      // 基本验证，至少需要选择一些症状信息
      if (this.symptoms.zangfu.length === 0 && this.symptoms.general.length === 0 && this.symptoms.daily.length === 0 && !this.symptoms.otherSymptoms) {
        ElMessage.warning('请至少提供一些症状信息');
        return false;
      }
      return true;
    },
    // 组合所有输入的数据
    combineData() {
      let data = [];

      // 添加患者基本信息
      if (this.patientInfo.name || this.patientInfo.gender || this.patientInfo.age) {
        data.push("患者基本信息：");
        if (this.patientInfo.name) data.push(`姓名：${this.patientInfo.name}`);
        if (this.patientInfo.gender) data.push(`性别：${this.patientInfo.gender}`);
        if (this.patientInfo.age) data.push(`年龄：${this.patientInfo.age}岁`);
        data.push("");
      }

      // 添加症状信息
      data.push("核心症状：");
      if (this.symptoms.zangfu.length > 0) {
        data.push(`脏腑相关症状：${this.symptoms.zangfu.join('、')}`);
      }
      if (this.symptoms.general.length > 0) {
        data.push(`全身伴随症状：${this.symptoms.general.join('、')}`);
      }
      if (this.symptoms.daily.length > 0) {
        data.push(`饮食睡眠二便：${this.symptoms.daily.join('、')}`);
      }
      if (this.symptoms.otherSymptoms) {
        data.push(`其他症状：${this.symptoms.otherSymptoms}`);
      }
      data.push("");

      // 添加发病情况
      data.push("发病与体质：");
      if (this.onset.situation) data.push(`发病特点：${this.onset.situation}`);
      if (this.onset.duration) data.push(`持续时间：${this.onset.duration}`);
      if (this.onset.constitution.length > 0) {
        data.push(`中医体质：${this.onset.constitution.join('、')}`);
      }
      if (this.onset.factors.length > 0) {
        data.push(`可能诱因：${this.onset.factors.join('、')}`);
      }
      if (this.onset.otherFactors) {
        data.push(`其他诱因：${this.onset.otherFactors}`);
      }
      data.push("");

      // 添加诊疗经过
      if (this.treatment.previousDisease || this.treatment.previousDiagnosis || this.treatment.previousTreatment || this.treatment.treatmentEffect) {
        data.push("诊疗与病史：");
        if (this.treatment.previousDisease) data.push(`既往病史：${this.treatment.previousDisease}`);
        if (this.treatment.previousDiagnosis) data.push(`既往中医诊断：${this.treatment.previousDiagnosis}`);
        if (this.treatment.previousTreatment) data.push(`曾接受的治疗：${this.treatment.previousTreatment}`);
        if (this.treatment.treatmentEffect) data.push(`治疗效果：${this.treatment.treatmentEffect}`);
      }
      return data.join("\n");
    },
    // 生成分析
    async generateAnalysis() {
      // 验证输入
      if (!this.validateInput()) {
        return;
      }
      this.loading = true;
      try {
        // 组合数据
        const patientData = this.combineData();
        // 构建提示信息
        let prompt = "我需要你使用中医内科的知识对我的症状进行分析，这是信息：\n";
        let msg = prompt + patientData;
        const encodedDescription = encodeURIComponent(msg);

        // 尝试导入数据传输工具
        import('@/lin/util/data-transfer').then(({
          saveData,
          generateUniqueId
        }) => {
          // 生成唯一ID
          const dataId = generateUniqueId();
          let infos = msg;
          // 存储数据到localStorage
          const saved = saveData(dataId, infos);
          console.log(dataId);
          if (saved) {
            // 传递数据ID作为参数
            try {
              const newWindow = window.open(`/#/chat?data_id=${dataId}`);
              if (!newWindow || newWindow.closed || typeof newWindow.closed === 'undefined') {
                // 如果window.open失败，使用location.href
                window.location.href = `/#/chat?data_id=${dataId}`;
              }
            } catch (error) {
              // 捕获任何可能的错误，使用location.href作为后备方案
              window.location.href = `/#/chat?data_id=${dataId}`;
            }
          }
        }).catch(error => {
          console.error('加载数据传输工具失败:', error);
          // 如果导入失败，回退到原来的方式
          window.location.href = `/#/chat?user_input=${encodedDescription}`;
        });
      } catch (error) {
        ElMessage.error('生成分析信息时出错');
        console.error(error);
      } finally {
        this.loading = false;
      }
    }
  }
};